.management_contract {
    width: 100%;
    height: 100%;
    padding: 20px;
    background-color: #fff;
    &-content {
        height: calc(100% - 32px);
        .action_btns {
            display: flex;
            justify-content: space-between;
            .left {
                .title {
                    font-weight: 700;
                    margin-right: 20px;
                }
                .status {
                    padding: 4px 8px;
                    border-radius: 5px;
                    border-width: 1px;
                    border-style: solid;
                    font-size: 12px;
                    font-weight: bold;
                }
            }
            .right {
                .common_color {
                    color: #096DD9;
                    margin-right: 20px;
                    cursor: pointer;
                    >span {
                        margin-left: 2px;
                    }
                }
                .ant-popconfirm {
                    .ant-popover-message-title {
                        padding-left: 0;
                    }
                    .title {
                        color: #666666;
                        margin-bottom: 20px;
                    }
                }
                 .notAllow {
                     opacity: 0.4;
                     cursor: not-allowed;
                 }
            }
        }
       
        .form-header {
            .ant-form {
                .ant-row {
                   margin: 15px 50px 0 0;
                }
            }
            .col {
                margin-right: 50px;
                .title {
                    margin-right: 8px;
                    color: rgba(34, 34, 34, 0.4);
                }
            }
        }
    }
    &-order-info {
        margin-top: 30px;
        .ant-form {
            .ant-row {
                margin: 20px 50px 0 0;
            }
        }
        .col {
            margin-top: 30px;
            margin-right: 80px;

            .title {
                margin-right: 8px;
                color: rgba(34, 34, 34, 0.4);
            }
        }
    }

    &-inventory-table {
        width: 100%;
        margin-top: 40px;
        display: flex;
        justify-content: space-between;
        .table-name {
            font-weight: 700;
            margin-bottom: 10px;
        }
        .table-left, .table-right {
            width: 48%;
        }
    }

    .ant-table {
        .summary_title {
            text-align: center;
            font-weight: 600;
        }
    }

    // 操作记录

    &-action {
        width: 100%;
        height: 100%;
        padding: 20px;
        background-color: #fff;

        .action-header {
            >span {
                font-weight: 600;
            }
        }

        .action-content {
            box-sizing: border-box;
            height: calc(100% - 80px);
             padding-top: 15px;
             margin: 10px 0;
             overflow: auto;

            .box {
                margin-bottom: 25px;

                .unite {
                    color: #ccc;
                    margin-right: 5px;
                }
            }
        }
    }
}

.saleManagementDrawer {
    .ant-table-wrapper {
        .ant-table-content {
            .ant-form-item {
                margin-bottom: 0px;
            }
        }
    }
}
@primary-color: #1890ff;